/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 1.5rem 0;
  font-weight: 300;
  text-align: center;
  margin-top: 0;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 3rem;
  display: inline-block;
  border: none;
  cursor: pointer;
}
.boton-amarillo:hover {
  background-color: #be7308;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 3rem;
  display: block;
  border: none;
  cursor: pointer;
}
.boton-amarillo-block:hover {
  background-color: #be7308;
}

.boton-verede {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 3rem;
  display: inline-block;
  border: none;
  cursor: pointer;
}
.boton-verede:hover {
  background-color: #609600;
}

/** UI layout**/
.inicio {
  background-image: url("../img/header.jpg");
}

.inicio {
  background-image: url("../img/header.webp");
}

.header {
  background-color: #333333;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: center;
  align-items: center;
  color: #FFFFFF;
}
.contenido-header .titulo {
  /* Para que el título ocupe todo el espacio entre barra y final y quede centrado */
  flex-grow: 1; /* ocupa el espacio disponible */
  display: flex;
  align-items: center; /* centra verticalmente dentro de este espacio */
  justify-content: center; /* centra horizontalmente */
  text-align: center;
  color: #FFFFFF;
}

.footer {
  padding: 0;
  background-color: #333333;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
  margin: 0;
}

/** Media Queries **/
.barra {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.barra a {
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin: 1rem;
    font-size: 1.8rem;
  }
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
  width: auto;
  max-width: 100%;
  object-fit: contain;
  display: inline-block;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    justify-content: space-between;
    margin-top: 10rem;
  }
}
.seccion {
  margin-bottom: 5rem;
}

.anuncio {
  border-radius: 5%;
  background-color: #eeeeee;
  border: 1px solid #4d4d4d;
  overflow: hidden;
  /* Mantén tus estilos existentes */
  display: flex;
  flex-direction: column;
  height: 100%; /* Fundamental */
}
.anuncio picture {
  display: block;
  width: 100%;
  aspect-ratio: 16/9;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}
.anuncio picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}
.anuncio .contenido-anuncio h3 {
  text-align: center;
  margin-bottom: 1rem;
  min-height: 3rem;
}
.anuncio .contenido-anuncio p {
  text-align: center;
  margin-bottom: 1.5rem;
  flex-grow: 1;
}
.anuncio h3, .anuncio p {
  margin: 0;
}
.anuncio .precio {
  color: #71B100;
  font-weight: 700;
  font-size: 2.5rem;
  text-align: center;
  margin: 1rem 0;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  justify-content: space-evenly; /* Distribución equitativa */
  gap: 1rem; /* Espacio entre elementos */
  max-width: 100%; /* Previene desbordamiento */
}
.iconos-caracteristicas li {
  flex: 0 1 auto; /* Flexibilidad controlada */
  display: flex;
  align-items: center; /* Centrado vertical */
  min-width: 0; /* Permite que el contenido se ajuste */
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem; /* Tamaño fijo para iconos */
  margin-right: 0.5rem;
  width: 2.4rem; /* Control de ancho */
  height: auto; /* Mantiene proporciones */
}
.iconos-caracteristicas li p {
  font-weight: 900;
  white-space: nowrap; /* Evita saltos de línea */
  overflow: hidden;
  text-overflow: ellipsis; /* (...) si el texto es largo */
}

.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg);
}

.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp);
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 3rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 3rem;
  font-weight: 900;
  color: #FFFFFF;
}
.imagen-contacto p {
  font-size: 2.3rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.blog {
  margin-top: 5%;
}

.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
    margin-top: 10rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}
.entrada-blog .texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .entrada-blog .texto-entrada a {
    margin-top: 0;
  }
}
.entrada-blog .texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.entrada-blog .texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}
.entrada-blog .texto-entrada span {
  color: #E08709;
}

.lugares-cercanos {
  border-radius: 5rem;
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
}
.lugares-cercanos blockquote {
  padding-left: 5rem;
  position: relative;
}
.lugares-cercanos blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}

/** paginas  internas **/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenido-nosotros p {
  text-align: center;
}

@media (min-width: 768px) {
  .contenido-descripcion {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.contenido-descripcion p {
  text-align: center;
}
.contenido-descripcion ul {
  text-decoration: none;
  list-style: none;
  text-align: center;
}